<template>
    <div class="map-view">
        <div id="main"></div>
    </div>
</template>
<script>
import geoData from '@/assets/data.json'
export default {
    data() {
        return {

        }
    },
    mounted() {
        let myMap = this.$echarts.init(document.getElementById('main'))
        this.$echarts.registerMap('china', geoData) //地图注册
        let option = {
            backgroundColor: 'rgb(121, 145, 209)', // 地图背景色
            geo: {

                map: 'china',
                aspectScale: 0.75, // 地图长宽比
                zoom: 1.1, //视角缩放比
                itemStyle: {
                    // 常规状态
                    normal: {
                        backgroundColor: 'rgb(147, 235, 248)',
                        borderWidth: 1,
                        areaColor: {
                            type: 'radial',//镜像渐变
                            x: 0.5,
                            y: 0.5,
                            r: 0.8,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: '#09132c' // 0%处的颜色
                                },
                                {
                                    offset: 1,
                                    color: '#274d68' // 100%处的颜色
                                }
                            ],
                            globalCoord: true

                        },
                        shadowColor:'rgb(58,120,192)',
                        // shadowColor:'rgb(244,164,96)',
                        shadowOffsetX:10,
                        shadowOffsetY:11,
                    },
                    // 高亮状态
                    emphasis: {
                        areaColor: 'rgb(46, 229, 206)', //选中颜色
                        borderWidth: 0.1
                    }
                },
                regions: [{
                    name: '南海诸岛',
                    itemStyle: {
                        opacity:0
                    }
                }]
            },
            series: [{
                map: 'china',
                type: 'map',
                zoom: 1.1,
                // 字体配置
                label: {
                    normal: {
                        show: true,
                        textStyle:{
                            color:'#1DE986'
                        },
                    },
                    emphasis:{
                        textStyle:{
                            color:'rgb(184,185,12)'
                        }
                    }
                },
                itemStyle: {
                    // 常规状态
                    normal: {
                        backgroundColor: 'rgb(147, 235, 248)',
                        borderWidth: 1,
                        areaColor: {
                            type: 'radial',//镜像渐变
                            x: 0.5,
                            y: 0.5,
                            r: 0.8,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: '#09132c' // 0%处的颜色
                                },
                                {
                                    offset: 1,
                                    color: '#274d68' // 100%处的颜色
                                }
                            ],
                            globalCoord: true

                        },
                    },
                    // 高亮状态
                    emphasis: {
                        areaColor: 'rgb(46, 229, 206)', //选中颜色
                        borderWidth: 0.1
                    }
                }
            }]
        }
        myMap.setOption(option)
        // console.log(geoData)
    }
}
</script>

<style lang="less">
.map-view {
    width: 100%;

    #main {
        height: 600px;
    }
}
</style>